// Service d'authentification pour l'application IKIGAI
// Gre les connexions, inscriptions et dconnexions d'utilisateurs

import { supabase, handleSupabaseError } from '../../shared/supabase';

class AuthService {
  // Rcuprer l'utilisateur actuel
  static async getCurrentUser() {
    try {
      const { data: { user }, error } = await supabase.auth.getUser();
      if (error) throw error;
      return user;
    } catch (error) {
      console.error('Erreur lors de la rcupration de l\'utilisateur:', error);
      return null;
    }
  }
  
  // Inscription avec email et mot de passe
  static async signUp(email, password) {
    try {
      const { data, error } = await supabase.auth.signUp({
        email,
        password
      });
      
      // Vrifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      // Vrifier si data.user existe
      if (!data || !data.user) {
        throw new Error("Erreur lors de l'inscription. Veuillez ressayer.");
      }
      
      return {
        user: data.user,
        success: true,
        message: 'Inscription russie! Vrifiez votre email pour confirmer votre compte.'
      };
    } catch (error) {
      console.error('Erreur lors de l\'inscription:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur appropri
      const errorMessage = handleSupabaseError(error);
      
      return {
        user: null,
        success: false,
        message: errorMessage || "Erreur lors de l'inscription. Veuillez ressayer."
      };
    }
  }
  
  // Connexion avec email et mot de passe
  static async signIn(email, password) {
    try {
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password
      });
      
      // Vrifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      // Vrifier si data.user existe
      if (!data || !data.user) {
        throw new Error("Erreur lors de la connexion. Veuillez vrifier vos identifiants.");
      }
      
      return {
        user: data.user,
        success: true,
        message: 'Connexion russie!'
      };
    } catch (error) {
      console.error('Erreur lors de la connexion:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur appropri
      const errorMessage = handleSupabaseError(error);
      
      return {
        user: null,
        success: false,
        message: errorMessage || "Identifiants incorrects. Veuillez ressayer."
      };
    }
  }
  
  // Connexion avec Google OAuth
  static async signInWithGoogle() {
    try {
      const redirectUrl = window.location.origin;
      console.log('URL de redirection OAuth configure:', redirectUrl);
      
      // Approche simplifie de gestion des sessions OAuth
      const authSessionId = Date.now().toString();
      
      // Stocker l'information d'onboarding uniquement dans localStorage
      // pour viter les problmes de synchronisation entre diffrents mcanismes de stockage
      const existingOnboardingStatus = localStorage.getItem('onboardingCompleted');
      
      // Si nous n'avons pas d'information explicite que l'onboarding est complt,
      // nous supposons qu'il doit tre effectu
      if (existingOnboardingStatus !== 'true') {
        console.log('Connexion Google - Statut onboarding non confirm, configuration pour redirection');
        localStorage.setItem('onboardingCompleted', 'false');
        localStorage.setItem('authSessionId', authSessionId);
      } else {
        console.log('Connexion Google - Statut onboarding dj confirm comme complt');
      }
      
      // Options pour Supabase OAuth avec persistance de session
      const options = {
        redirectTo: redirectUrl,
        persistSession: true
      };
      
      const { error } = await supabase.auth.signInWithOAuth({
        provider: 'google',
        options: options
      });
      
      // Vrifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      return {
        success: true,
        message: 'Redirection vers Google...'
      };
    } catch (error) {
      console.error('Erreur lors de la connexion avec Google:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur appropri
      const errorMessage = handleSupabaseError(error);
      
      return {
        success: false,
        message: errorMessage || "Erreur lors de la connexion avec Google. Veuillez ressayer."
      };
    }
  }
  
  // Dconnexion
  static async signOut() {
    try {
      const { error } = await supabase.auth.signOut();
      
      // Vrifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      return {
        success: true,
        message: 'Dconnexion russie!'
      };
    } catch (error) {
      console.error('Erreur lors de la dconnexion:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur appropri
      const errorMessage = handleSupabaseError(error);
      
      return {
        success: false,
        message: errorMessage
      };
    }
  }
  
  // Vrifier si un utilisateur est connect
  static async isAuthenticated() {
    try {
      const user = await this.getCurrentUser();
      return !!user;
    } catch (error) {
      return false;
    }
  }
  
  // Rinitialisation de mot de passe
  static async resetPassword(email) {
    try {
      const { error } = await supabase.auth.resetPasswordForEmail(email, {
        redirectTo: `${window.location.origin}/reset-password`,
      });
      
      // Vrifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      return {
        success: true,
        message: 'Email de rinitialisation envoy avec succs!'
      };
    } catch (error) {
      console.error('Erreur lors de la rinitialisation du mot de passe:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur appropri
      const errorMessage = handleSupabaseError(error);
      
      return {
        success: false,
        message: errorMessage || "Erreur lors de l'envoi de l'email de rinitialisation. Veuillez ressayer."
      };
    }
  }
}

export default AuthService;