import React, { useState, useEffect } from 'react';
import './App.css';
import API from './backend/api';
import WelcomePage from './frontend/pages/WelcomePage';
import HomePage from './frontend/pages/HomePage';
import IslandView from './frontend/pages/IslandView';
import SignupPage from './frontend/pages/SignupPage';
import OnboardingJourney from './frontend/pages/OnboardingJourney';
import OnboardingAnalysisPage from './frontend/pages/OnboardingAnalysisPage';

function App() {
  const [progress, setProgress] = useState(null);
  const [islands, setIslands] = useState([]);
  const [challenges, setChallenges] = useState([]);
  const [exercises, setExercises] = useState([]);
  const [modules, setModules] = useState([]);
  const [userSettings, setUserSettings] = useState({
    notifications: true,
    sounds: true,
    darkMode: false, // Default dark mode setting
    dataPrivacy: 'standard',
    language: 'fr',
    dailyReminders: false,
    meditationSounds: false
  });
  
  const [selectedIsland, setSelectedIsland] = useState(null);
  const [showWelcome, setShowWelcome] = useState(false);
  const [showSignup, setShowSignup] = useState(false);
  const [showOnboarding, setShowOnboarding] = useState(false);
  const [showOnboardingAnalysis, setShowOnboardingAnalysis] = useState(false);
  const [onboardingResponses, setOnboardingResponses] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  
  // Charger les donnes initiales
  useEffect(() => {
    const initApp = async () => {
      try {
        // Charger le contenu statique
        const allIslands = API.content.getAllIslands();
        const allChallenges = API.content.getAllChallenges();
        const allExercises = API.content.getAllExercises();
        const allModules = API.content.getAllModules();
        
        setIslands(allIslands);
        setChallenges(allChallenges);
        setExercises(allExercises);
        setModules(allModules);
        
        // Vrifier si l'utilisateur est connect
        const isAuth = await API.auth.isAuthenticated();
        
        // Charger la progression
        const userProgress = await API.progress.getProgress();
        setProgress(userProgress);
        
        // Charger les paramtres utilisateur
        const settings = await API.progress.getUserSettings();
        if (settings) {
          setUserSettings(settings);
          // Apply dark mode immediately
          if (settings.darkMode) {
            document.documentElement.classList.add('dark');
          } else {
            document.documentElement.classList.remove('dark');
          }
        } else {
           // Apply default dark mode state if no settings found
           if (userSettings.darkMode) {
             document.documentElement.classList.add('dark');
           } else {
             document.documentElement.classList.remove('dark');
           }
        }
        
        // Dtecter un retour d'inscription/connexion via Google
        const isOAuthRedirect = window.location.hash && 
                               (window.location.hash.includes('access_token') || 
                                window.location.hash.includes('error'));
                                
        if (isOAuthRedirect) {
          console.log("Dtection d'un retour d'authentification OAuth");
          // Nettoyer l'URL aprs dtection du retour OAuth
          window.history.replaceState(null, document.title, window.location.pathname);
        }
        
        // Vrifier si l'onboarding est complt depuis les donnes de progression
        // ou depuis localStorage si prsent (notamment pour les retours de Google OAuth)
        let isOnboardingCompleted;
        
        // Si valeur dans localStorage, l'utiliser en priorit
        if (localStorage.getItem('onboardingCompleted') !== null) {
          isOnboardingCompleted = localStorage.getItem('onboardingCompleted') === 'true';
          console.log("Utilisation de la valeur d'onboarding depuis localStorage:", isOnboardingCompleted);
        } else {
          // Sinon utiliser les donnes de progression
          isOnboardingCompleted = userProgress.moduleResponses && 
                                 userProgress.moduleResponses.onboarding && 
                                 userProgress.moduleResponses.onboarding.completedAt;
          console.log("Utilisation de la valeur d'onboarding depuis les donnes de progression:", isOnboardingCompleted);
        }
        
        console.log("tat onboarding:", isOnboardingCompleted ? "Complt" : "Non complt");
        console.log("Donnes onboarding:", userProgress.moduleResponses?.onboarding);
        
        if (isAuth) {
          // Utilisateur connect
          console.log("Utilisateur authentifi");
          
          // Lors d'un retour d'OAuth Google, vrifier si l'utilisateur est nouveau
          if (isOAuthRedirect) {
            // Vrifier dans plusieurs sources si l'utilisateur doit tre redirig vers l'onboarding
            const localStorageOnboarding = localStorage.getItem('onboardingCompleted') === 'false';
            const sessionStorageOnboarding = sessionStorage.getItem('onboardingCompleted') === 'false';
            const cookieOnboarding = document.cookie.includes('onboardingRequired=true');
            
            console.log('tat onboarding aprs OAuth - localStorage:', localStorageOnboarding);
            console.log('tat onboarding aprs OAuth - sessionStorage:', sessionStorageOnboarding);
            console.log('tat onboarding aprs OAuth - cookie:', cookieOnboarding);
            
            // Si l'une des sources indique que l'onboarding est requis
            if (localStorageOnboarding || sessionStorageOnboarding || cookieOnboarding) {
              console.log("Utilisateur authentifi via Google - Redirection vers l'onboarding");
              setShowWelcome(false);
              setShowSignup(false);
              setShowOnboarding(true);
              
              // Nettoyer toutes les sources aprs utilisation
              localStorage.removeItem('onboardingCompleted');
              sessionStorage.removeItem('onboardingCompleted');
              document.cookie = 'onboardingRequired=; path=/; expires=Thu, 01 Jan 1970 00:00:00 GMT';
              
              setIsLoading(false);
              return;
            }
          }
          
          if (isOnboardingCompleted) {
            // Si l'utilisateur est inscrit et a complt l'onboarding, afficher la page principale
            setShowWelcome(false);
            setShowSignup(false);
            setShowOnboarding(false);
            console.log("Affichage de la page principale - Onboarding dj complt");
          } else {
            // Si l'utilisateur est inscrit mais n'a pas complt l'onboarding
            setShowOnboarding(true);
            setShowWelcome(false);
            setShowSignup(false);
            console.log("Affichage de l'onboarding - Onboarding non complt");
          }
        } else {
          // Utilisateur non connect
          console.log("Utilisateur non authentifi");
          if (isOnboardingCompleted) {
            // Si l'onboarding est complt mais l'utilisateur n'est pas connect, afficher la page de connexion
            setShowSignup(true);
            setShowWelcome(false);
            setShowOnboarding(false);
            console.log("Affichage de la page de connexion - Onboarding dj complt");
          } else {
            // Nouvel utilisateur, afficher la page d'accueil
            setShowWelcome(true);
            setShowSignup(false);
            setShowOnboarding(false);
            console.log("Affichage de la page d'accueil - Nouvel utilisateur");
          }
        }
        
        // Nettoyer localStorage aprs utilisation
        localStorage.removeItem('onboardingCompleted');
      } catch (error) {
        console.error("Erreur lors de l'initialisation:", error);
      } finally {
        setIsLoading(false);
      }
    };
    
    initApp();
  }, []);
  
  // Fonction pour mettre  jour les paramtres globaux et appliquer les changements
  const updateGlobalSettings = (newSettings) => {
    setUserSettings(newSettings);
    // Apply dark mode change
    if (newSettings.darkMode) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
    // TODO: Apply other settings like language if needed
  };

  // Grer la compltion d'un module
  const handleCompleteModule = async (moduleId, islandId) => {
    try {
      const updatedProgress = await API.progress.completeModule(moduleId, islandId);
      setProgress(updatedProgress);
    } catch (error) {
      console.error("Erreur lors de la compltion du module:", error);
    }
  };
  
  // Grer la compltion d'un dfi
  const handleCompleteChallenge = async (challengeId) => {
    try {
      const updatedProgress = await API.progress.completeChallenge(challengeId);
      setProgress(updatedProgress);
    } catch (error) {
      console.error("Erreur lors de la compltion du dfi:", error);
    }
  };
  
  // Grer la dconnexion
  const handleLogout = async () => {
    try {
      await API.auth.signOut();
      
      // Vrifier si l'utilisateur a dj complt l'onboarding
      // Note: userProgress n'est pas utilis ici, car nous rinitialisons simplement la vue
      await API.progress.getProgress();
      // Vrifier si l'onboarding est complt
      
      // Si l'onboarding est dj complt, afficher directement la page de connexion
      // sans rediriger vers l'onboarding
      setShowSignup(true);
      setShowOnboarding(false);
    } catch (error) {
      console.error("Erreur lors de la dconnexion:", error);
      alert("Une erreur est survenue lors de la dconnexion.");
    }
  };
  
  // Fonction pour rinitialiser la progression - actuellement non utilise
  // mais conserve pour une utilisation future potentielle
  const handleResetProgress = async () => {
    try {
      const resetProgress = API.progress.resetAllData();
      setProgress(resetProgress);
      console.log("Progression rinitialise avec succs");
    } catch (error) {
      console.error("Erreur lors de la rinitialisation de la progression:", error);
    }
  };
  
  // Grer la compltion de l'onboarding
  const handleOnboardingComplete = async (responses) => {
    try {
      console.log("App: Compltion de l'onboarding avec rponses:", responses);
      
      // Ajouter un timestamp de compltion si non prsent
      const responsesWithTimestamp = {
        ...responses,
        completedAt: responses.completedAt || new Date().toISOString()
      };
      
      // Dsactiver TOUS les autres crans d'abord
      setShowWelcome(false);
      setShowSignup(false);
      setShowOnboarding(false);
      setSelectedIsland(null);
      setIsLoading(false);
      
      // IMPORTANT: Activer la page d'analyse AVANT de stocker les rponses
      // pour viter les problmes de timing avec le rendu conditionnel
      setShowOnboardingAnalysis(true);
      console.log("Activation de la page d'analyse d'onboarding");
      
      // Ensuite seulement stocker les rponses
      setOnboardingResponses(responsesWithTimestamp);
      
      // Marquer l'onboarding comme complt dans localStorage pour rfrence future
      localStorage.setItem('onboardingCompleted', 'true');
      
      console.log("Activation de la page d'analyse complte. tat:", {
        showOnboardingAnalysis: true,
        showOnboarding: false,
        showSignup: false,
        showWelcome: false,
        isLoading: false,
        responsesStored: !!responsesWithTimestamp
      });
      
    } catch (error) {
      console.error("Erreur lors de la compltion de l'onboarding:", error);
      // En cas d'erreur, continuer vers la page principale
      setShowOnboarding(false);
      setShowOnboardingAnalysis(false);
      setIsLoading(false);
    }
  };
  
  // Grer la fin de l'analyse
  const handleAnalysisComplete = async (responses) => {
    try {
      console.log("App: Analyse termine pour les rponses:", responses);
      
      // Ajouter des points pour avoir complt l'onboarding
      const updatedProgress = {...progress};
      updatedProgress.totalPoints = (updatedProgress.totalPoints || 0) + 200;
      
      // S'assurer que moduleResponses.onboarding existe et est marqu comme complt
      if (!updatedProgress.moduleResponses) {
        updatedProgress.moduleResponses = {};
      }
      if (!updatedProgress.moduleResponses.onboarding) {
        updatedProgress.moduleResponses.onboarding = {};
      }
      updatedProgress.moduleResponses.onboarding = {
        ...responses,
        completedAt: responses.completedAt || new Date().toISOString()
      };
      
      try {
        await API.progress.saveProgress(updatedProgress);
        setProgress(updatedProgress);
      } catch (saveError) {
        console.warn("Erreur lors de la sauvegarde de la progression:", saveError);
        // Continuer malgr l'erreur
      }
      
      // Fermer la page d'analyse et passer  l'application principale
      setShowOnboardingAnalysis(false);
      setOnboardingResponses(null);
      
      // S'assurer que tous les autres crans sont dsactivs
      setShowWelcome(false);
      setShowSignup(false);
      setShowOnboarding(false);
      
      // Lger dlai pour laisser React mettre  jour les tats
      setTimeout(() => {
        console.log("Transition vers la page principale termine");
      }, 100);
      
    } catch (error) {
      console.error("Erreur lors de la finalisation de l'analyse:", error);
      // En cas d'erreur, continuer vers la page principale
      setShowOnboardingAnalysis(false);
      setOnboardingResponses(null);
      setShowWelcome(false);
      setShowSignup(false);
      setShowOnboarding(false);
    }
  };
  
  // Affichage pendant le chargement
  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-screen flex-col bg-gray-50">
        <div className="flex items-center">
          <div className="flex items-center justify-center bg-gradient-to-br from-green-400 to-blue-500 rounded-full w-9 h-9 text-white">
            <span className="text-xl"></span>
          </div>
          <span className="ml-2 text-xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-green-500 to-blue-500">IKIGAI</span>
        </div>
        <div className="mt-8 relative w-16 h-16">
          <div className="absolute top-0 left-0 w-full h-full rounded-full border-4 border-gray-200"></div>
          <div className="absolute top-0 left-0 w-full h-full rounded-full border-4 border-t-transparent border-l-transparent animate-spin border-blue-500"></div>
        </div>
        <span className="mt-4 text-gray-600">Chargement...</span>
      </div>
    );
  }
  
  // PRIORIT 1: Si l'analyse des rponses d'onboarding est active, afficher la page d'analyse en priorit
  if (showOnboardingAnalysis) {
    console.log("Affichage de la page d'analyse des rponses:", onboardingResponses);
    return (
      <OnboardingAnalysisPage 
        responses={onboardingResponses || {}}
        onAnalysisComplete={handleAnalysisComplete}
      />
    );
  }
  
  // PRIORIT 2: Si une le est slectionne, afficher sa vue
  if (selectedIsland) {
    return (
      <IslandView 
        island={selectedIsland} 
        onReturn={() => setSelectedIsland(null)} 
        globalProgress={progress} 
        onCompleteModule={handleCompleteModule}
        modules={modules}
      />
    );
  }
  
  // PRIORIT 3: Si l'onboarding est actif, l'afficher
  if (showOnboarding) {
    return (
      <OnboardingJourney 
        onComplete={handleOnboardingComplete}
        onCancel={() => {
          setShowOnboarding(false);
          setShowSignup(true);
        }}
      />
    );
  }
  
  // PRIORIT 4: Si la page d'inscription est active, l'afficher
  if (showSignup) {
    return (
      <SignupPage
        onComplete={async () => {
          // Rcuprer les donnes de progression directement depuis l'API pour avoir les informations les plus  jour
          const userProgress = await API.progress.getProgress();
          
          // Rcuprer l'tat d'onboarding depuis localStorage ou le calculer depuis userProgress
          let isOnboardingCompleted = localStorage.getItem('onboardingCompleted') === 'true';
          
          // Si la valeur n'est pas dans localStorage, vrifier dans les donnes de progression
          if (localStorage.getItem('onboardingCompleted') === null) {
            isOnboardingCompleted = userProgress.moduleResponses && 
                                     userProgress.moduleResponses.onboarding && 
                                     userProgress.moduleResponses.onboarding.completedAt;
          }
          
          // Supprimer l'item du localStorage aprs utilisation
          localStorage.removeItem('onboardingCompleted');
          
          console.log("Aprs connexion - tat onboarding:", isOnboardingCompleted ? "Complt" : "Non complt");
          console.log("Donnes onboarding:", userProgress.moduleResponses?.onboarding);
          
          setShowSignup(false);
          
          // Vrifier que userProgress est bien dfini avant d'utiliser ses proprits
          if (userProgress && Object.keys(userProgress).length > 0) {
            // Rediriger vers l'onboarding uniquement si l'utilisateur ne l'a pas dj complt
            if (isOnboardingCompleted) {
              // Si l'onboarding est dj complt, afficher directement la page principale
              setShowOnboarding(false);
              console.log("Redirection vers la page principale - Onboarding dj complt");
            } else {
              // Sinon, afficher l'onboarding
              setShowOnboarding(true);
              console.log("Redirection vers l'onboarding - Onboarding non complt");
            }
          } else {
            // En cas de donnes manquantes, afficher l'onboarding par dfaut
            setShowOnboarding(true);
            console.log("Redirection vers l'onboarding - Donnes de progression indisponibles");
          }
          
          // Nettoyer localStorage si prsent
          localStorage.removeItem('onboardingCompleted');
        }}
        onCancel={() => {
          setShowSignup(false);
          setShowWelcome(true);
        }}
      />
    );
  }
  
  // Si la page d'accueil est active, l'afficher
  if (showWelcome) {
    return (
      <WelcomePage 
        onStart={() => {
          setShowWelcome(false);
          setShowSignup(true);
        }}
        onSignIn={() => {
          setShowWelcome(false);
          setShowSignup(true);
          localStorage.setItem('isReconnecting', 'true');
        }}
      />
    );
  }
  
  // Afficher la page principale
  return (
    <div className={`App ${userSettings.darkMode ? 'dark' : ''}`}> 
      <HomePage 
        progress={progress}
        islands={islands}
        challenges={challenges}
        exercises={exercises}
        onSelectIsland={setSelectedIsland}
        onCompleteChallenge={handleCompleteChallenge}
        onLogout={handleLogout}
        userSettings={userSettings}
        onUpdateSettings={updateGlobalSettings}
      />
    </div>
  );
}

export default App;