// Modle pour les les thmatiques

export class Island {
  constructor(id, name, description, color, icon, mascot, modules, badges) {
    this.id = id;
    this.name = name;
    this.description = description;
    this.color = color;
    this.icon = icon;
    this.mascot = mascot;
    this.modules = modules || 4;
    this.badges = badges || {};
  }

  static fromJSON(json) {
    return new Island(
      json.id,
      json.name,
      json.description,
      json.color,
      json.icon,
      json.mascot,
      json.modules,
      json.badges
    );
  }
}

