// Service de stockage pour l'application IKIGAI
// Gre la persistance des donnes utilisateur (progression, badges, etc.)

import { supabase } from '../../shared/supabase';
import { UserProgress } from '../models/UserProgress';

class StorageService {
  // Cl utilise pour le stockage local
  static LOCAL_STORAGE_KEY = 'ikigai_progress';
  
  // Obtenir la structure de donnes initiale pour un nouvel utilisateur
  static getInitialProgress(userId = null) {
    return new UserProgress(userId).toJSON();
  }
  
  // Rcuprer la progression depuis Supabase
  static async getProgress() {
    try {
      // Vrifier si l'utilisateur est connect
      const { data: { user } } = await supabase.auth.getUser();
      
      if (user) {
        // Rcuprer les donnes depuis Supabase
        const { data, error } = await supabase
          .from('user_progress')
          .select('*')
          .eq('user_id', user.id)
          .single();
        
        if (error) throw error;
        
        if (data) {
          return JSON.parse(data.progress_data);
        }
      }
      
      // Si pas d'utilisateur connect ou pas de donnes, utiliser localStorage
      return this.getProgressSync();
    } catch (error) {
      console.error('Erreur lors de la rcupration des donnes:', error);
      return this.getProgressSync();
    }
  }
  
  // Rcuprer les paramtres utilisateur depuis Supabase
  static async getUserSettings() {
    // Dfinir les paramtres par dfaut
    const defaultSettings = {
      notifications: true,
      sounds: true,
      darkMode: false,
      dataPrivacy: 'standard',
      language: 'fr',
      dailyReminders: false,
      meditationSounds: false
    };
    
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) {
        console.log("Utilisateur non connect, utilisation des paramtres par dfaut.");
        return defaultSettings;
      }
      
      const { data, error } = await supabase
        .from('user_settings')
        .select('settings')
        .eq('user_id', user.id)
        .single();
        
      if (error) {
        if (error.code === 'PGRST116') { // PGRST116: No rows found
          console.log('Aucun paramtre trouv pour cet utilisateur, utilisation des paramtres par dfaut.');
          return defaultSettings;
        } else {
          console.error('Erreur lors de la rcupration des paramtres utilisateur:', error);
          // En cas d'erreur de requte, utiliser les paramtres par dfaut
          return defaultSettings;
        }
      }
      
      if (data && data.settings) {
        // Traiter selon le type de donnes retourn
        if (typeof data.settings === 'string') {
          try {
            // Essayer de parser la chane JSON
            const parsedSettings = JSON.parse(data.settings);
            // Fusionner avec les paramtres par dfaut pour s'assurer que tous les champs sont prsents
            return { ...defaultSettings, ...parsedSettings };
          } catch (parseError) {
            console.error('Erreur lors du parsing des paramtres JSON:', parseError);
            return defaultSettings;
          }
        } else if (typeof data.settings === 'object') {
          // Si c'est dj un objet, le fusionner avec les paramtres par dfaut
          return { ...defaultSettings, ...data.settings };
        } else {
          console.warn('Format des paramtres inattendu:', data.settings);
          return defaultSettings;
        }
      } else {
        // Pas de paramtres trouvs
        return defaultSettings;
      }
    } catch (error) {
      console.error('Erreur gnrale lors de la rcupration des paramtres utilisateur:', error);
      // En cas d'erreur gnrale, utiliser les paramtres par dfaut
      return defaultSettings;
    }
  }

  // Version synchrone utilisant localStorage (fallback)
  static getProgressSync() {
    const savedProgress = localStorage.getItem(this.LOCAL_STORAGE_KEY);
    if (savedProgress) {
      try {
        return JSON.parse(savedProgress);
      } catch (e) {
        console.error('Erreur lors du parsing des donnes locales:', e);
        return this.getInitialProgress();
      }
    }
    return this.getInitialProgress();
  }
  
  // Sauvegarder la progression
  static async saveProgress(progressData) {
    try {
      // Sauvegarder en local d'abord (fallback)
      localStorage.setItem(this.LOCAL_STORAGE_KEY, JSON.stringify(progressData));
      
      // Vrifier si l'utilisateur est connect
      const { data: { user } } = await supabase.auth.getUser();
      
      if (user) {
        // Sauvegarder dans Supabase
        const { error } = await supabase
          .from('user_progress')
          .upsert({
            user_id: user.id,
            progress_data: JSON.stringify(progressData),
            updated_at: new Date()
          });
        
        if (error) throw error;
      }
      
      return progressData;
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des donnes:', error);
      return progressData;
    }
  }
  
  // Complter un module
  static async completeModule(moduleId, islandId) {
    try {
      const progress = await this.getProgress();
      
      // Utiliser la classe UserProgress pour encapsuler la logique
      const userProgress = UserProgress.fromJSON(progress);
      userProgress.completeModule(moduleId, islandId);
      const updatedProgress = userProgress.toJSON();
      
      // Sauvegarder les changements
      return await this.saveProgress(updatedProgress);
    } catch (error) {
      console.error('Erreur lors de la compltion du module:', error);
      return this.completeModuleSync(moduleId, islandId);
    }
  }
  
  // Version synchrone pour complter un module (fallback)
  static completeModuleSync(moduleId, islandId) {
    const progress = this.getProgressSync();
    
    // Utiliser la classe UserProgress pour encapsuler la logique
    const userProgress = UserProgress.fromJSON(progress);
    userProgress.completeModule(moduleId, islandId);
    const updatedProgress = userProgress.toJSON();
    
    // Sauvegarder les changements
    localStorage.setItem(this.LOCAL_STORAGE_KEY, JSON.stringify(updatedProgress));
    return updatedProgress;
  }
  
  // Complter un dfi quotidien
  static async completeChallenge(challengeId) {
    try {
      const progress = await this.getProgress();
      
      // Utiliser la classe UserProgress pour encapsuler la logique
      const userProgress = UserProgress.fromJSON(progress);
      userProgress.completeChallenge(challengeId);
      const updatedProgress = userProgress.toJSON();
      
      // Sauvegarder les changements
      return await this.saveProgress(updatedProgress);
    } catch (error) {
      console.error('Erreur lors de la compltion du dfi:', error);
      return this.completeChallengeSync(challengeId);
    }
  }
  
  // Version synchrone pour complter un dfi (fallback)
  static completeChallengeSync(challengeId) {
    const progress = this.getProgressSync();
    
    // Utiliser la classe UserProgress pour encapsuler la logique
    const userProgress = UserProgress.fromJSON(progress);
    userProgress.completeChallenge(challengeId);
    const updatedProgress = userProgress.toJSON();
    
    // Sauvegarder les changements
    localStorage.setItem(this.LOCAL_STORAGE_KEY, JSON.stringify(updatedProgress));
    return updatedProgress;
  }
  
  // Sauvegarder les rponses d'onboarding dans Supabase
  static async saveOnboardingResponses(responses) {
    try {
      console.log("StorageService: Sauvegarde des rponses d'onboarding:", responses);
      
      // Traiter les valeurs personnalises si ncessaire
      let responsesToSave = { ...responses };
      
      // Si des entres se terminent par _custom, les fusionner correctement
      Object.keys(responsesToSave).forEach(key => {
        if (key.endsWith('_custom')) {
          const mainQuestionId = key.replace('_custom', '');
          console.log(`StorageService: Fusion des donnes personnalises pour ${mainQuestionId}`);
          
          // Ajouter les donnes personnalises  l'objet principal
          responsesToSave.customValues = responsesToSave.customValues || {};
          responsesToSave.customValues[mainQuestionId] = responsesToSave[key];
          
          // Supprimer l'entre _custom spare pour viter la duplication
          delete responsesToSave[key];
        }
      });
      
      // S'assurer que nous avons un timestamp de compltion
      if (!responsesToSave.completedAt) {
        responsesToSave.completedAt = new Date().toISOString();
      }
      
      // Sauvegarder en local d'abord (fallback)
      const progress = await this.getProgress();
      const userProgress = UserProgress.fromJSON(progress);
      userProgress.saveModuleResponses('onboarding', responsesToSave);
      localStorage.setItem(this.LOCAL_STORAGE_KEY, JSON.stringify(userProgress.toJSON()));
      
      // Vrifier si l'utilisateur est connect
      const { data: { user } } = await supabase.auth.getUser();
      
      if (user) {
        console.log("StorageService: Utilisateur connect, sauvegarde dans Supabase pour:", user.id);
        
        // Approche simplifie: utiliser une seule stratgie principale avec fallback
        try {
          // STRATGIE PRINCIPALE: Utiliser la table user_responses
          console.log("StorageService: Tentative de sauvegarde dans user_responses");
          const { error } = await supabase
            .from('user_responses')
            .upsert({
              user_id: user.id,
              module_id: 'onboarding',
              responses: responsesToSave,
              created_at: new Date()
            }, { onConflict: 'user_id,module_id' });
          
          if (error) {
            console.warn("StorageService: Erreur avec user_responses:", error.message);
            
            // FALLBACK: Tenter avec onboarding_responses ddie
            console.log("StorageService: Tentative avec table ddie onboarding_responses");
            const { error: fallbackError } = await supabase
              .from('onboarding_responses')
              .upsert({
                user_id: user.id,
                responses: responsesToSave,
                updated_at: new Date()
              }, { onConflict: 'user_id' });
              
            if (fallbackError) {
              console.warn("StorageService: Fallback galement chou:", fallbackError.message);
              throw fallbackError;
            } else {
              console.log("StorageService: Sauvegarde dans onboarding_responses russie");
            }
          } else {
            console.log("StorageService: Sauvegarde dans user_responses russie");
          }
        } catch (err) {
          console.error("StorageService: chec de toutes les stratgies:", err);
          
          // Sauvegarder localement en cas d'chec total
          localStorage.setItem('onboarding_responses_backup', JSON.stringify({
            user_id: user.id,
            responses: responsesToSave,
            timestamp: new Date().toISOString(),
            error: err.message
          }));
        }
        
        // Dans tous les cas, mettre  jour user_progress pour compatibilit
        const updatedProgress = userProgress.toJSON();
        await this.saveProgress(updatedProgress);
      } else {
        console.log("StorageService: Aucun utilisateur connect, sauvegarde locale uniquement");
      }
      
      return userProgress.toJSON();
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des rponses d\'onboarding:', error);
      return this.saveOnboardingResponsesSync(responses);
    }
  }
  
  // Version synchrone pour sauvegarder les rponses d'onboarding (fallback)
  static saveOnboardingResponsesSync(responses) {
    // Traiter les valeurs personnalises comme dans la version asynchrone
    let responsesToSave = { ...responses };
    
    // Si des entres se terminent par _custom, les fusionner correctement
    Object.keys(responsesToSave).forEach(key => {
      if (key.endsWith('_custom')) {
        const mainQuestionId = key.replace('_custom', '');
        
        // Ajouter les donnes personnalises  l'objet principal
        responsesToSave.customValues = responsesToSave.customValues || {};
        responsesToSave.customValues[mainQuestionId] = responsesToSave[key];
        
        // Supprimer l'entre _custom spare pour viter la duplication
        delete responsesToSave[key];
      }
    });
    
    // S'assurer que nous avons un timestamp de compltion
    if (!responsesToSave.completedAt) {
      responsesToSave.completedAt = new Date().toISOString();
    }
    
    const progress = this.getProgressSync();
    const userProgress = UserProgress.fromJSON(progress);
    userProgress.saveModuleResponses('onboarding', responsesToSave);
    const updatedProgress = userProgress.toJSON();
    localStorage.setItem(this.LOCAL_STORAGE_KEY, JSON.stringify(updatedProgress));
    return updatedProgress;
  }
  
  // Sauvegarder les rponses d'un module
  static async saveModuleResponses(moduleId, responses) {
    // Pour l'onboarding, utiliser la mthode spcifique
    if (moduleId === 'onboarding') {
      return await this.saveOnboardingResponses(responses);
    }
    
    try {
      const progress = await this.getProgress();
      
      // Utiliser la classe UserProgress pour encapsuler la logique
      const userProgress = UserProgress.fromJSON(progress);
      userProgress.saveModuleResponses(moduleId, responses);
      const updatedProgress = userProgress.toJSON();
      
      // Sauvegarder les changements
      return await this.saveProgress(updatedProgress);
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des rponses:', error);
      return this.saveModuleResponsesSync(moduleId, responses);
    }
  }
  
  // Version synchrone pour sauvegarder les rponses (fallback)
  static saveModuleResponsesSync(moduleId, responses) {
    // Pour l'onboarding, utiliser la mthode spcifique
    if (moduleId === 'onboarding') {
      return this.saveOnboardingResponsesSync(responses);
    }
    
    const progress = this.getProgressSync();
    
    // Utiliser la classe UserProgress pour encapsuler la logique
    const userProgress = UserProgress.fromJSON(progress);
    userProgress.saveModuleResponses(moduleId, responses);
    const updatedProgress = userProgress.toJSON();
    
    // Sauvegarder les changements
    localStorage.setItem(this.LOCAL_STORAGE_KEY, JSON.stringify(updatedProgress));
    return updatedProgress;
  }
  
  // Rinitialiser toutes les donnes
  static resetAllData() {
    localStorage.removeItem(this.LOCAL_STORAGE_KEY);
    return this.getInitialProgress();
  }
}

export default StorageService;