// Service d'analyse IA pour l'application IKIGAI
// Analyse les rponses utilisateur et gnre des recommandations personnalises
// Version amliore avec traitement de langage naturel

import { supabase, handleSupabaseError } from '../../shared/supabase';
import { AI_CONFIG } from '../../config';
import ContentService from './ContentService';

class AIRecommendationService {
  // Catgories de recommandations
  static RECOMMENDATION_CATEGORIES = AI_CONFIG.categories.reduce((obj, category) => {
    obj[category.toUpperCase()] = category;
    return obj;
  }, {});

  // Modle de sentiment
  static SENTIMENT_MAP = {
    POSITIVE: 'positive',
    NEUTRAL: 'neutral',
    NEGATIVE: 'negative'
  };

  // Analyser les rponses d'un utilisateur et gnrer des recommandations
  static async analyzeUserResponses(userId) {
    try {
      if (!userId) {
        const { data: { user } } = await supabase.auth.getUser();
        if (!user) return { success: false, error: 'Utilisateur non connect' };
        userId = user.id;
      }

      // Rcuprer les rponses de l'utilisateur
      const { data: userResponses, error: responsesError } = await supabase
        .from('user_responses')
        .select('*')
        .eq('user_id', userId);

      if (responsesError) {
        console.error('Erreur lors de la rcupration des rponses:', responsesError);
        return { success: false, error: handleSupabaseError(responsesError) };
      }

      // Rcuprer les donnes de progression
      const { data: progressData, error: progressError } = await supabase
        .from('user_progress')
        .select('*')
        .eq('user_id', userId)
        .single();

      if (progressError && progressError.code !== 'PGRST116') {
        console.error('Erreur lors de la rcupration de la progression:', progressError);
        return { success: false, error: handleSupabaseError(progressError) };
      }

      // Rcuprer les mtadonnes utilisateur
      const { data: userData } = await supabase
        .from('users')
        .select('metadata')
        .eq('id', userId)
        .maybeSingle();

      // Analyser les donnes et gnrer des recommandations
      const recommendations = await this.generateEnhancedRecommendations(
        userResponses, 
        progressData,
        userData?.metadata
      );

      // Sauvegarder les recommandations dans Supabase
      await this.saveRecommendations(userId, recommendations);

      return { 
        success: true, 
        recommendations,
        analysisVersion: AI_CONFIG.version
      };
    } catch (error) {
      console.error('Erreur lors de l\'analyse des rponses:', error);
      return { success: false, error: handleSupabaseError(error) };
    }
  }

  // Gnrer des recommandations amliores avec NLP
  static async generateEnhancedRecommendations(userResponses, progressData, userMetadata = {}) {
    try {
      // Initialiser les scores par catgorie avec les nouvelles catgories
      const categoryScores = Object.values(this.RECOMMENDATION_CATEGORIES).reduce((acc, category) => {
        acc[category] = 0;
        return acc;
      }, {});

      // Agrger toutes les rponses textuelles pour analyse NLP
      const textualResponses = this.extractTextualResponses(userResponses);
      
      // Raliser l'analyse NLP des rponses textuelles
      const nlpAnalysis = await this.performNLPAnalysis(textualResponses);
      
      // Intgrer l'analyse NLP dans les scores
      this.integrateNLPAnalysisToScores(nlpAnalysis, categoryScores);
      
      // Analyser les rponses classiques comme avant
      this.analyzeClassicResponses(userResponses, categoryScores);
      
      // Analyser les donnes de progression si disponibles
      if (progressData && progressData.progress_data) {
        const parsedProgress = typeof progressData.progress_data === 'string' 
          ? JSON.parse(progressData.progress_data) 
          : progressData.progress_data;
        
        this.analyzeProgressData(parsedProgress, categoryScores);
      }
      
      // Intgrer les mtadonnes utilisateur dans l'analyse
      if (userMetadata) {
        this.analyzeUserMetadata(userMetadata, categoryScores);
      }

      // Normaliser les scores
      this.normalizeScores(categoryScores);

      // Gnrer des recommandations bases sur les scores enrichis
      return this.createEnhancedRecommendationsFromScores(categoryScores, nlpAnalysis);
    } catch (error) {
      console.error('Erreur lors de la gnration des recommandations amliores:', error);
      // Fallback en cas d'erreur : utiliser l'ancien systme
      return this.generateRecommendations(userResponses, progressData);
    }
  }

  // Extraire toutes les rponses textuelles pour analyse NLP
  static extractTextualResponses(userResponses) {
    const textualResponses = [];
    
    if (userResponses && userResponses.length > 0) {
      userResponses.forEach(response => {
        const responses = response.responses;
        if (!responses) return;
        
        // Extraire toutes les rponses textuelles
        if (typeof responses === 'object') {
          Object.entries(responses).forEach(([questionId, answer]) => {
            if (answer && typeof answer === 'string' && answer.length > 3) {
              textualResponses.push({
                text: answer,
                moduleId: response.module_id,
                questionId
              });
            }
          });
        }
      });
    }
    
    return textualResponses;
  }

  // Raliser l'analyse NLP des rponses textuelles
  static async performNLPAnalysis(textualResponses) {
    try {
      // Simulation d'une analyse NLP (remplacer par appel  une API relle)
      const analysisResults = {
        keyTopics: this.simulateTopicExtraction(textualResponses),
        sentiments: this.simulateSentimentAnalysis(textualResponses),
        intentions: this.simulateIntentionAnalysis(textualResponses)
      };
      
      return analysisResults;
    } catch (error) {
      console.error('Erreur lors de l\'analyse NLP:', error);
      return {
        keyTopics: [],
        sentiments: {},
        intentions: []
      };
    }
  }

  // Simulation de l'extraction de sujets ( remplacer par API relle)
  static simulateTopicExtraction(textualResponses) {
    const topics = {
      stress: 0,
      mditation: 0,
      travail: 0,
      sommeil: 0,
      exercice: 0,
      alimentation: 0,
      relations: 0,
      productivit: 0,
      crativit: 0,
      concentration: 0
    };
    
    const keywords = {
      stress: ['stress', 'anxit', 'pression', 'inquitude', 'tension'],
      mditation: ['mditation', 'pleine conscience', 'mindfulness', 'calme', 'respiration'],
      travail: ['travail', 'emploi', 'carrire', 'bureau', 'professionnel'],
      sommeil: ['sommeil', 'dormir', 'fatigue', 'repos', 'nuit'],
      exercice: ['exercice', 'sport', 'activit physique', 'entranement', 'bouger'],
      alimentation: ['alimentation', 'nourriture', 'repas', 'manger', 'dite'],
      relations: ['relations', 'famille', 'amis', 'social', 'communication'],
      productivit: ['productivit', 'efficacit', 'organisation', 'rendement', 'tches'],
      crativit: ['crativit', 'imagination', 'innovation', 'ides', 'artistique'],
      concentration: ['concentration', 'focus', 'attention', 'mental', 'clart']
    };
    
    textualResponses.forEach(response => {
      const text = response.text.toLowerCase();
      
      // Pour chaque thme, vrifier la prsence de mots-cls
      Object.entries(keywords).forEach(([topic, words]) => {
        words.forEach(word => {
          if (text.includes(word)) {
            topics[topic] += 1;
          }
        });
      });
    });
    
    // Convertir en tableau tri par importance
    return Object.entries(topics)
      .filter(([_, count]) => count > 0)
      .sort((a, b) => b[1] - a[1])
      .map(([topic, count]) => ({ topic, count }));
  }

  // Simulation d'analyse de sentiment ( remplacer par API relle)
  static simulateSentimentAnalysis(textualResponses) {
    // Mots  connotation positive et ngative
    const positiveWords = ['bon', 'bien', 'heureux', 'content', 'satisfait', 'positif', 'aime', 'apprcier', 
                          'espoir', 'motiv', 'nergique', 'serein', 'confiant'];
    const negativeWords = ['mauvais', 'difficile', 'stress', 'anxieux', 'inquiet', 'ngatif', 'peur', 
                          'fatigu', 'puis', 'dprim', 'frustr', 'pnible', 'triste'];
    
    const moduleSentiments = {};
    const overallSentiment = {
      positive: 0,
      negative: 0,
      neutral: 0
    };
    
    textualResponses.forEach(response => {
      const text = response.text.toLowerCase();
      const moduleId = response.moduleId;
      
      // Compter les mots positifs et ngatifs
      let positiveCount = 0;
      let negativeCount = 0;
      
      positiveWords.forEach(word => {
        if (text.includes(word)) positiveCount++;
      });
      
      negativeWords.forEach(word => {
        if (text.includes(word)) negativeCount++;
      });
      
      // Dterminer le sentiment dominant
      let sentiment;
      if (positiveCount > negativeCount) {
        sentiment = this.SENTIMENT_MAP.POSITIVE;
        overallSentiment.positive++;
      } else if (negativeCount > positiveCount) {
        sentiment = this.SENTIMENT_MAP.NEGATIVE;
        overallSentiment.negative++;
      } else {
        sentiment = this.SENTIMENT_MAP.NEUTRAL;
        overallSentiment.neutral++;
      }
      
      // Enregistrer le sentiment par module
      if (!moduleSentiments[moduleId]) {
        moduleSentiments[moduleId] = {
          positive: 0,
          negative: 0,
          neutral: 0
        };
      }
      
      moduleSentiments[moduleId][sentiment]++;
    });
    
    return {
      byModule: moduleSentiments,
      overall: overallSentiment
    };
  }

  // Simulation d'analyse d'intention ( remplacer par API relle)
  static simulateIntentionAnalysis(textualResponses) {
    const intentions = [];
    
    // Modles d'intention bass sur des phrases cls
    const intentionPatterns = [
      {
        name: 'amliorer_stress',
        patterns: ['rduire stress', 'grer stress', 'moins stress', 'mieux grer', 'calmer']
      },
      {
        name: 'amliorer_sommeil',
        patterns: ['mieux dormir', 'amliorer sommeil', 'problme sommeil', 'insomnie']
      },
      {
        name: 'augmenter_productivit',
        patterns: ['plus productif', 'mieux organis', 'amliorer concentration', 'plus efficace']
      },
      {
        name: 'quilibre_vie',
        patterns: ['quilibre', 'harmonie', 'vie personnelle', 'temps famille', 'moins travailler']
      }
    ];
    
    textualResponses.forEach(response => {
      const text = response.text.toLowerCase();
      
      intentionPatterns.forEach(intention => {
        intention.patterns.forEach(pattern => {
          if (text.includes(pattern)) {
            intentions.push({
              intention: intention.name,
              text: response.text,
              moduleId: response.moduleId
            });
          }
        });
      });
    });
    
    return intentions;
  }

  // Intgrer les rsultats de l'analyse NLP dans les scores
  static integrateNLPAnalysisToScores(nlpAnalysis, categoryScores) {
    // Utiliser les thmes identifis pour augmenter les scores des catgories correspondantes
    nlpAnalysis.keyTopics.forEach(topic => {
      const { topic: topicName, count } = topic;
      
      // Mappings entre thmes et catgories
      const topicCategoryMapping = {
        stress: this.RECOMMENDATION_CATEGORIES.STRESS,
        mditation: this.RECOMMENDATION_CATEGORIES.MINDFULNESS,
        travail: this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY,
        sommeil: this.RECOMMENDATION_CATEGORIES.BALANCE,
        exercice: this.RECOMMENDATION_CATEGORIES.WELLBEING,
        alimentation: this.RECOMMENDATION_CATEGORIES.WELLBEING,
        relations: this.RECOMMENDATION_CATEGORIES.SOCIAL,
        productivit: this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY,
        crativit: this.RECOMMENDATION_CATEGORIES.GROWTH,
        concentration: this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY
      };
      
      const category = topicCategoryMapping[topicName];
      if (category) {
        categoryScores[category] += count * 2;
      }
    });
    
    // Utiliser les rsultats de l'analyse de sentiment
    const { overall } = nlpAnalysis.sentiments;
    
    // Si beaucoup de sentiments ngatifs, augmenter stress et quilibre
    if (overall.negative > overall.positive) {
      categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += overall.negative * 2;
      categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += overall.negative;
    }
    
    // Si beaucoup de sentiments positifs, augmenter croissance personnelle
    if (overall.positive > overall.negative) {
      categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += overall.positive;
    }
    
    // Interprter les intentions
    nlpAnalysis.intentions.forEach(intention => {
      switch (intention.intention) {
        case 'amliorer_stress':
          categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 3;
          categoryScores[this.RECOMMENDATION_CATEGORIES.MINDFULNESS] += 2;
          break;
        case 'amliorer_sommeil':
          categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 3;
          categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 2;
          break;
        case 'augmenter_productivit':
          categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 3;
          categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += 1;
          break;
        case 'quilibre_vie':
          categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 3;
          categoryScores[this.RECOMMENDATION_CATEGORIES.SOCIAL] += 2;
          break;
        default:
          // Cas par dfaut pour les intentions non reconnues
          categoryScores[this.RECOMMENDATION_CATEGORIES.GENERAL] += 1;
          break;
      }
    });
  }

  // Analyser les rponses selon la mthode classique
  static analyzeClassicResponses(userResponses, categoryScores) {
    // Analyser les rponses pour identifier les tendances
    if (userResponses && userResponses.length > 0) {
      userResponses.forEach(response => {
        // Analyse des rponses par module
        if (response.module_id) {
          if (response.module_id.startsWith('mindfulness')) {
            this.analyzeModuleResponses(response, categoryScores, this.RECOMMENDATION_CATEGORIES.MINDFULNESS);
          } else if (response.module_id.startsWith('productivity')) {
            this.analyzeModuleResponses(response, categoryScores, this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY);
          } else if (response.module_id.startsWith('stress')) {
            this.analyzeModuleResponses(response, categoryScores, this.RECOMMENDATION_CATEGORIES.STRESS);
          } else if (response.module_id.startsWith('balance')) {
            this.analyzeModuleResponses(response, categoryScores, this.RECOMMENDATION_CATEGORIES.BALANCE);
          } else if (response.module_id === 'onboarding') {
            this.analyzeOnboardingResponses(response, categoryScores);
          }
        }
      });
    }
  }

  // Analyser les mtadonnes utilisateur
  static analyzeUserMetadata(userMetadata, categoryScores) {
    try {
      // Analyse des prfrences utilisateur bases sur les mtadonnes
      if (userMetadata.interests && Array.isArray(userMetadata.interests)) {
        userMetadata.interests.forEach(interest => {
          // Mappings entre intrts et catgories
          if (interest.includes('mditation') || interest.includes('yoga')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.MINDFULNESS] += 3;
          }
          if (interest.includes('productivit') || interest.includes('efficacit')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 3;
          }
          if (interest.includes('stress') || interest.includes('relaxation')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 3;
          }
          if (interest.includes('quilibre') || interest.includes('bien-tre')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 3;
          }
          if (interest.includes('alimentation') || interest.includes('fitness')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 3;
          }
          if (interest.includes('social') || interest.includes('communication')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.SOCIAL] += 3;
          }
          if (interest.includes('apprendre') || interest.includes('dveloppement')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += 3;
          }
        });
      }
      
      // Analyse de l'ge et du secteur d'activit
      if (userMetadata.age) {
        const age = parseInt(userMetadata.age);
        if (age > 45) {
          // Les personnes plus ges peuvent avoir des besoins spcifiques d'quilibre
          categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 2;
        } else if (age < 30) {
          // Les plus jeunes peuvent tre plus intresss par la productivit et la croissance
          categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 2;
          categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += 2;
        }
      }
      
      if (userMetadata.industry) {
        // Adaptation base sur le secteur d'activit
        const industry = userMetadata.industry.toLowerCase();
        if (industry.includes('tech') || industry.includes('informatique')) {
          categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 2;
          categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 2;
        } else if (industry.includes('sant') || industry.includes('mdical')) {
          categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 3;
          categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 2;
        } else if (industry.includes('finance') || industry.includes('banque')) {
          categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 3;
          categoryScores[this.RECOMMENDATION_CATEGORIES.MINDFULNESS] += 2;
        }
      }
    } catch (error) {
      console.error('Erreur lors de l\'analyse des mtadonnes utilisateur:', error);
    }
  }

  // Crer des recommandations amliores bases sur les scores enrichis
  static createEnhancedRecommendationsFromScores(categoryScores, nlpAnalysis) {
    const recommendations = [];
    const allModules = ContentService.getAllModules();
    
    // Trier les catgories par score (du plus lev au plus bas)
    const sortedCategories = Object.entries(categoryScores)
      .sort((a, b) => b[1] - a[1]);
    
    // Gnrer des recommandations pour les 3 catgories principales
    for (let i = 0; i < Math.min(3, sortedCategories.length); i++) {
      const [category, score] = sortedCategories[i];
      if (score >= 5) { // Seuil minimum pour recommander
        // Trouver des modules dans cette catgorie
        const modulesInCategory = allModules.filter(module => 
          module.islandId === category || 
          module.tags?.includes(category)
        );
        
        if (modulesInCategory.length > 0) {
          // Prendre jusqu' 2 modules de cette catgorie
          const recommendedModules = modulesInCategory.slice(0, 2);
          
          recommendedModules.forEach(module => {
            recommendations.push({
              type: 'module',
              id: module.id,
              title: module.title,
              category,
              reason: this.getEnhancedRecommendationReason(category, score, nlpAnalysis),
              score,
              tags: module.tags || []
            });
          });
        }
      }
    }
    
    // Ajouter des recommandations gnrales bases sur les thmes identifis
    if (nlpAnalysis.keyTopics.length > 0) {
      const topTopic = nlpAnalysis.keyTopics[0].topic;
      recommendations.push({
        type: 'general',
        title: `Explorez "${topTopic}" pour amliorer votre bien-tre`,
        description: `Nos analyses montrent que le thme "${topTopic}" est important pour vous. Dcouvrez nos modules pour approfondir ce sujet.`,
        category: this.RECOMMENDATION_CATEGORIES.GENERAL,
        reason: `Bas sur vos rponses mentionnant "${topTopic}"`,
        score: 8
      });
    }
    
    // S'assurer d'avoir au moins une recommandation
    if (recommendations.length === 0) {
      recommendations.push({
        type: 'general',
        title: 'Explorez de nouveaux horizons',
        description: 'Dcouvrez diffrentes approches pour amliorer votre bien-tre au travail',
        category: this.RECOMMENDATION_CATEGORIES.GENERAL,
        reason: 'Bas sur votre profil global',
        score: 7
      });
    }
    
    return recommendations;
  }

  // Obtenir une raison personnalise amliore pour la recommandation
  static getEnhancedRecommendationReason(category, score, nlpAnalysis) {
    const reasons = {
      [this.RECOMMENDATION_CATEGORIES.MINDFULNESS]: [
        'Vous semblez intress par les pratiques de pleine conscience',
        'Ces exercices pourraient vous aider  dvelopper votre attention au moment prsent',
        'Bas sur vos rponses concernant la mditation et la concentration'
      ],
      [this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY]: [
        'Pour amliorer votre organisation et votre efficacit',
        'Ces techniques pourraient vous aider  mieux grer votre temps',
        'Bas sur vos objectifs de productivit'
      ],
      [this.RECOMMENDATION_CATEGORIES.STRESS]: [
        'Pour vous aider  mieux grer le stress au quotidien',
        'Ces pratiques pourraient rduire votre niveau d\'anxit',
        'Bas sur vos rponses concernant la gestion du stress'
      ],
      [this.RECOMMENDATION_CATEGORIES.BALANCE]: [
        'Pour vous aider  trouver un meilleur quilibre vie pro/perso',
        'Ces modules favorisent l\'harmonie entre les diffrentes sphres de votre vie',
        'Bas sur votre recherche d\'quilibre'
      ],
      [this.RECOMMENDATION_CATEGORIES.WELLBEING]: [
        'Pour amliorer votre bien-tre gnral',
        'Ces modules vous aideront  prendre soin de vous',
        'Pour renforcer votre vitalit au quotidien'
      ],
      [this.RECOMMENDATION_CATEGORIES.SOCIAL]: [
        'Pour dvelopper vos comptences relationnelles',
        'Ces modules vous aideront  mieux communiquer avec les autres',
        'Pour favoriser des relations harmonieuses'
      ],
      [this.RECOMMENDATION_CATEGORIES.GROWTH]: [
        'Pour stimuler votre dveloppement personnel',
        'Ces modules vous aideront  atteindre votre plein potentiel',
        'Pour nourrir votre croissance personnelle'
      ],
      [this.RECOMMENDATION_CATEGORIES.GENERAL]: [
        'Recommandation personnalise base sur votre profil',
        'Pour explorer de nouvelles approches de bien-tre',
        'Pour diversifier vos pratiques'
      ]
    };
    
    // Personnalisation base sur l'analyse NLP
    if (nlpAnalysis.sentiments.overall.negative > nlpAnalysis.sentiments.overall.positive) {
      if (category === this.RECOMMENDATION_CATEGORIES.STRESS || 
          category === this.RECOMMENDATION_CATEGORIES.BALANCE) {
        return 'Nos analyses montrent que vous pourriez bnficier de techniques pour rduire le stress et retrouver plus de srnit';
      }
    }
    
    if (nlpAnalysis.keyTopics.length > 0) {
      const topTopic = nlpAnalysis.keyTopics[0].topic;
      if ((topTopic === 'stress' && category === this.RECOMMENDATION_CATEGORIES.STRESS) ||
          (topTopic === 'mditation' && category === this.RECOMMENDATION_CATEGORIES.MINDFULNESS) ||
          (topTopic === 'productivit' && category === this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY) ||
          (topTopic === 'sommeil' && category === this.RECOMMENDATION_CATEGORIES.BALANCE)) {
        return `Recommand pour vous car vous avez mentionn "${topTopic}" dans vos rponses`;
      }
    }
    
    // Slectionner une raison par dfaut dans la catgorie
    const categoryReasons = reasons[category] || reasons[this.RECOMMENDATION_CATEGORIES.GENERAL];
    const randomIndex = Math.floor(Math.random() * categoryReasons.length);
    return categoryReasons[randomIndex];
  }

  // Analyser les rponses d'un module spcifique
  static analyzeModuleResponses(moduleResponse, categoryScores, category) {
    try {
      const responses = moduleResponse.responses;
      if (!responses) return;

      // Analyse base sur le type de module
      if (typeof responses === 'object') {
        // Parcourir toutes les rponses du module
        Object.entries(responses).forEach(([questionId, answer]) => {
          // Analyse des rponses  choix multiples
          if (answer && typeof answer === 'string') {
            // Analyse simplifie base sur des mots-cls
            if (answer.includes('stress') || answer.includes('anxit')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 2;
            }
            if (answer.includes('concentration') || answer.includes('focus')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 2;
            }
            if (answer.includes('mditation') || answer.includes('respiration')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.MINDFULNESS] += 2;
            }
            if (answer.includes('quilibre') || answer.includes('harmonie')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 2;
            }
            if (answer.includes('nergie') || answer.includes('sant')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 2;
            }
            if (answer.includes('relations') || answer.includes('communication')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.SOCIAL] += 2;
            }
            if (answer.includes('apprendre') || answer.includes('dveloppement')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += 2;
            }
          }
          // Analyse des rponses numriques (chelles)
          else if (typeof answer === 'number') {
            // Si la rponse est faible (< 3 sur 5), augmenter le score de la catgorie
            if (answer < 3) {
              categoryScores[category] += (3 - answer) * 2;
            }
          }
          // Analyse des rponses  choix multiples
          else if (Array.isArray(answer)) {
            answer.forEach(choice => {
              // Analyse base sur les choix spcifiques
              if (choice === 'a' || choice === 'b') { // Supposons que a et b sont des choix lis au stress
                categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 1;
              }
              if (choice === 'c' || choice === 'd') { // Supposons que c et d sont des choix lis  la productivit
                categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 1;
              }
            });
          }
        });

        // Augmenter le score de la catgorie principale du module
        categoryScores[category] += 3;
      }
    } catch (error) {
      console.error('Erreur lors de l\'analyse des rponses du module:', error);
    }
  }

  // Analyser les rponses d'onboarding
  static analyzeOnboardingResponses(onboardingResponse, categoryScores) {
    try {
      const responses = onboardingResponse.responses;
      if (!responses) return;

      // Analyse des objectifs et prfrences initiales
      if (responses.goals) {
        if (Array.isArray(responses.goals)) {
          responses.goals.forEach(goal => {
            if (goal.includes('stress') || goal.includes('anxit')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 3;
            }
            if (goal.includes('productivit') || goal.includes('concentration')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 3;
            }
            if (goal.includes('mditation') || goal.includes('pleine conscience')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.MINDFULNESS] += 3;
            }
            if (goal.includes('quilibre') || goal.includes('bien-tre')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 3;
            }
            if (goal.includes('sant') || goal.includes('nergie')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 3;
            }
            if (goal.includes('social') || goal.includes('relations')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.SOCIAL] += 3;
            }
            if (goal.includes('apprendre') || goal.includes('dveloppement')) {
              categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += 3;
            }
          });
        } else if (typeof responses.goals === 'string') {
          // Analyse du texte de l'objectif
          const goalText = responses.goals.toLowerCase();
          if (goalText.includes('stress') || goalText.includes('anxit')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 3;
          }
          if (goalText.includes('productivit') || goalText.includes('concentration')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 3;
          }
          if (goalText.includes('mditation') || goalText.includes('pleine conscience')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.MINDFULNESS] += 3;
          }
          if (goalText.includes('quilibre') || goalText.includes('bien-tre')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 3;
          }
          if (goalText.includes('sant') || goalText.includes('nergie')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 3;
          }
          if (goalText.includes('social') || goalText.includes('relations')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.SOCIAL] += 3;
          }
          if (goalText.includes('apprendre') || goalText.includes('dveloppement')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += 3;
          }
        }
      }

      // Analyse des niveaux de stress dclars
      if (responses.stress_level && typeof responses.stress_level === 'number') {
        categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += responses.stress_level * 2;
      }

      // Analyse des prfrences d'activits
      if (responses.preferred_activities && Array.isArray(responses.preferred_activities)) {
        responses.preferred_activities.forEach(activity => {
          if (activity.includes('mditation') || activity.includes('yoga')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.MINDFULNESS] += 2;
          }
          if (activity.includes('organisation') || activity.includes('planification')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 2;
          }
          if (activity.includes('relaxation') || activity.includes('dtente')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 2;
          }
          if (activity.includes('quilibre') || activity.includes('harmonie')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 2;
          }
          if (activity.includes('sant') || activity.includes('fitness')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 2;
          }
          if (activity.includes('social') || activity.includes('communication')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.SOCIAL] += 2;
          }
          if (activity.includes('apprendre') || activity.includes('dveloppement')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += 2;
          }
        });
      }
    } catch (error) {
      console.error('Erreur lors de l\'analyse des rponses d\'onboarding:', error);
    }
  }

  // Analyser les donnes de progression
  static analyzeProgressData(progressData, categoryScores) {
    try {
      // Analyser les modules complts
      if (progressData.completedModules) {
        const completedModuleIds = Object.keys(progressData.completedModules);
        
        // Identifier les domaines o l'utilisateur est actif
        completedModuleIds.forEach(moduleId => {
          if (moduleId.startsWith('mindfulness')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.MINDFULNESS] += 1;
          } else if (moduleId.startsWith('productivity')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY] += 1;
          } else if (moduleId.startsWith('stress')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 1;
          } else if (moduleId.startsWith('balance')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 1;
          } else if (moduleId.startsWith('wellbeing')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 1;
          } else if (moduleId.startsWith('social')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.SOCIAL] += 1;
          } else if (moduleId.startsWith('growth')) {
            categoryScores[this.RECOMMENDATION_CATEGORIES.GROWTH] += 1;
          }
        });

        // Identifier les domaines o l'utilisateur est moins actif (pour recommandations)
        const allCategories = Object.values(this.RECOMMENDATION_CATEGORIES).filter(cat => cat !== 'general');
        allCategories.forEach(category => {
          const modulesInCategory = completedModuleIds.filter(id => id.startsWith(category)).length;
          if (modulesInCategory === 0) {
            // Augmenter le score pour recommander des modules dans cette catgorie
            categoryScores[category] += 5;
          }
        });
      }

      // Analyser le score de bien-tre
      if (progressData.wellnessScore) {
        // Si le score est bas, recommander des modules de stress et d'quilibre
        if (progressData.wellnessScore < 50) {
          categoryScores[this.RECOMMENDATION_CATEGORIES.STRESS] += 4;
          categoryScores[this.RECOMMENDATION_CATEGORIES.BALANCE] += 3;
          categoryScores[this.RECOMMENDATION_CATEGORIES.WELLBEING] += 3;
        }
      }
    } catch (error) {
      console.error('Erreur lors de l\'analyse des donnes de progression:', error);
    }
  }

  // Normaliser les scores pour quilibrer les recommandations
  static normalizeScores(categoryScores) {
    // Trouver le score maximum
    const maxScore = Math.max(...Object.values(categoryScores));
    
    // Normaliser tous les scores sur une chelle de 0  10
    if (maxScore > 0) {
      Object.keys(categoryScores).forEach(category => {
        categoryScores[category] = Math.round((categoryScores[category] / maxScore) * 10);
      });
    }
  }

  // Mthode de fallback pour la gnration de recommandations
  static generateRecommendations(userResponses, progressData) {
    // Initialiser les scores par catgorie
    const categoryScores = {
      [this.RECOMMENDATION_CATEGORIES.MINDFULNESS]: 0,
      [this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY]: 0,
      [this.RECOMMENDATION_CATEGORIES.STRESS]: 0,
      [this.RECOMMENDATION_CATEGORIES.BALANCE]: 0,
      [this.RECOMMENDATION_CATEGORIES.GENERAL]: 0
    };

    // Analyser les rponses pour identifier les tendances
    if (userResponses && userResponses.length > 0) {
      userResponses.forEach(response => {
        // Analyse des rponses par module
        if (response.module_id) {
          if (response.module_id.startsWith('mindfulness')) {
            this.analyzeModuleResponses(response, categoryScores, this.RECOMMENDATION_CATEGORIES.MINDFULNESS);
          } else if (response.module_id.startsWith('productivity')) {
            this.analyzeModuleResponses(response, categoryScores, this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY);
          } else if (response.module_id.startsWith('stress')) {
            this.analyzeModuleResponses(response, categoryScores, this.RECOMMENDATION_CATEGORIES.STRESS);
          } else if (response.module_id.startsWith('balance')) {
            this.analyzeModuleResponses(response, categoryScores, this.RECOMMENDATION_CATEGORIES.BALANCE);
          } else if (response.module_id === 'onboarding') {
            this.analyzeOnboardingResponses(response, categoryScores);
          }
        }
      });
    }

    // Analyser les donnes de progression si disponibles
    if (progressData && progressData.progress_data) {
      const parsedProgress = typeof progressData.progress_data === 'string' 
        ? JSON.parse(progressData.progress_data) 
        : progressData.progress_data;
      
      this.analyzeProgressData(parsedProgress, categoryScores);
    }

    // Normaliser les scores
    this.normalizeScores(categoryScores);

    // Gnrer des recommandations bases sur les scores
    return this.createRecommendationsFromScores(categoryScores);
  }

  // Mthode de fallback pour la cration de recommandations
  static createRecommendationsFromScores(categoryScores) {
    const recommendations = [];
    const allModules = ContentService.getAllModules();
    
    // Trier les catgories par score (du plus lev au plus bas)
    const sortedCategories = Object.entries(categoryScores)
      .filter(([category]) => category !== this.RECOMMENDATION_CATEGORIES.GENERAL)
      .sort((a, b) => b[1] - a[1]);
    
    // Gnrer des recommandations pour les 2 catgories principales
    for (let i = 0; i < Math.min(2, sortedCategories.length); i++) {
      const [category, score] = sortedCategories[i];
      if (score >= 5) { // Seuil minimum pour recommander
        // Trouver des modules dans cette catgorie
        const modulesInCategory = allModules.filter(module => module.islandId === category);
        
        if (modulesInCategory.length > 0) {
          // Prendre jusqu' 2 modules de cette catgorie
          const recommendedModules = modulesInCategory.slice(0, 2);
          
          recommendedModules.forEach(module => {
            recommendations.push({
              type: 'module',
              id: module.id,
              title: module.title,
              category,
              reason: this.getRecommendationReason(category, score),
              score
            });
          });
        }
      }
    }
    
    // Ajouter des recommandations gnrales si ncessaire
    if (recommendations.length < 3 && categoryScores[this.RECOMMENDATION_CATEGORIES.GENERAL] > 0) {
      recommendations.push({
        type: 'general',
        title: 'Explorez de nouveaux horizons',
        description: 'Dcouvrez diffrentes approches pour amliorer votre bien-tre au travail',
        category: this.RECOMMENDATION_CATEGORIES.GENERAL,
        reason: 'Bas sur votre profil global',
        score: categoryScores[this.RECOMMENDATION_CATEGORIES.GENERAL]
      });
    }
    
    return recommendations;
  }

  // Obtenir une raison personnalise pour la recommandation (mthode de fallback)
  static getRecommendationReason(category, score) {
    const reasons = {
      [this.RECOMMENDATION_CATEGORIES.MINDFULNESS]: [
        'Vous semblez intress par les pratiques de pleine conscience',
        'Ces exercices pourraient vous aider  dvelopper votre attention au moment prsent',
        'Bas sur vos rponses concernant la mditation et la concentration'
      ],
      [this.RECOMMENDATION_CATEGORIES.PRODUCTIVITY]: [
        'Pour amliorer votre organisation et votre efficacit',
        'Ces techniques pourraient vous aider  mieux grer votre temps',
        'Bas sur vos objectifs de productivit'
      ],
      [this.RECOMMENDATION_CATEGORIES.STRESS]: [
        'Pour vous aider  mieux grer le stress au quotidien',
        'Ces pratiques pourraient rduire votre niveau d\'anxit',
        'Bas sur vos rponses concernant la gestion du stress'
      ],
      [this.RECOMMENDATION_CATEGORIES.BALANCE]: [
        'Pour vous aider  trouver un meilleur quilibre vie pro/perso',
        'Ces modules favorisent l\'harmonie entre les diffrentes sphres de votre vie',
        'Bas sur votre recherche d\'quilibre'
      ],
      [this.RECOMMENDATION_CATEGORIES.GENERAL]: [
        'Recommandation personnalise base sur votre profil',
        'Pour explorer de nouvelles approches de bien-tre',
        'Pour diversifier vos pratiques'
      ]
    };
    
    // Slectionner une raison alatoire dans la catgorie
    const categoryReasons = reasons[category] || reasons[this.RECOMMENDATION_CATEGORIES.GENERAL];
    const randomIndex = Math.floor(Math.random() * categoryReasons.length);
    return categoryReasons[randomIndex];
  }

  // Sauvegarder les recommandations dans Supabase
  static async saveRecommendations(userId, recommendations) {
    try {
      // Vrifier si la table user_recommendations existe
      const { error: tableCheckError } = await supabase
        .from('user_recommendations')
        .select('id')
        .limit(1);
      
      // Si la table n'existe pas, la crer
      if (tableCheckError && (tableCheckError.code === 'PGRST116' || tableCheckError.message.includes('does not exist'))) {
        // Stocker les recommandations dans user_progress en attendant
        const { data: progressData, error: progressError } = await supabase
          .from('user_progress')
          .select('*')
          .eq('user_id', userId)
          .single();
        
        if (!progressError) {
          let parsedProgress = typeof progressData.progress_data === 'string' 
            ? JSON.parse(progressData.progress_data) 
            : progressData.progress_data;
          
          // Ajouter les recommandations au progress_data
          parsedProgress.recommendations = {
            items: recommendations,
            generatedAt: new Date().toISOString(),
            analysisVersion: AI_CONFIG.version
          };
          
          // Mettre  jour user_progress
          await supabase
            .from('user_progress')
            .update({
              progress_data: JSON.stringify(parsedProgress),
              updated_at: new Date()
            })
            .eq('user_id', userId);
        }
      } else {
        // Si la table existe, insrer/mettre  jour les recommandations
        await supabase
          .from('user_recommendations')
          .upsert({
            user_id: userId,
            recommendations: recommendations,
            generated_at: new Date(),
            updated_at: new Date(),
            analysis_version: AI_CONFIG.version,
            analysis_metadata: {
              modelVersion: 'v1.2',
              categories: Object.values(this.RECOMMENDATION_CATEGORIES),
              timestamp: new Date().toISOString()
            }
          }, { onConflict: 'user_id' });
      }
      
      return { success: true };
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des recommandations:', error);
      return { success: false, error: handleSupabaseError(error) };
    }
  }

  // Rcuprer les recommandations d'un utilisateur
  static async getUserRecommendations(userId) {
    try {
      if (!userId) {
        const { data: { user } } = await supabase.auth.getUser();
        if (!user) return { success: false, error: 'Utilisateur non connect' };
        userId = user.id;
      }

      // Vrifier d'abord dans la table user_recommendations
      const { data: recommendationsData, error: recommendationsError } = await supabase
        .from('user_recommendations')
        .select('*')
        .eq('user_id', userId)
        .single();
      
      // Si les recommandations existent dans la table ddie
      if (!recommendationsError && recommendationsData) {
        return { 
          success: true, 
          recommendations: recommendationsData.recommendations,
          generatedAt: recommendationsData.generated_at,
          analysisVersion: recommendationsData.analysis_version || AI_CONFIG.version
        };
      }
      
      // Sinon, vrifier dans user_progress
      const { data: progressData, error: progressError } = await supabase
        .from('user_progress')
        .select('*')
        .eq('user_id', userId)
        .single();
      
      if (!progressError && progressData) {
        const parsedProgress = typeof progressData.progress_data === 'string' 
          ? JSON.parse(progressData.progress_data) 
          : progressData.progress_data;
        
        if (parsedProgress.recommendations) {
          return { 
            success: true, 
            recommendations: parsedProgress.recommendations.items,
            generatedAt: parsedProgress.recommendations.generatedAt,
            analysisVersion: parsedProgress.recommendations.analysisVersion || AI_CONFIG.version
          };
        }
      }
      
      // Si aucune recommandation n'est trouve, en gnrer de nouvelles
      return await this.analyzeUserResponses(userId);
    } catch (error) {
      console.error('Erreur lors de la rcupration des recommandations:', error);
      return { success: false, error: handleSupabaseError(error) };
    }
  }
}

export default AIRecommendationService;